@charset "utf-8";

// Dimensions
$max-content-width: 800px;

@import
  "variables",
  "themes",
  "layout",
  "base",
  "distill",
  "gallery",
  "people",
  "links";

;

///This is for Conferences page

.flex-container {
  display: flex;
  gap: 10px; /* Space between the image and video */
}

.flex-container img {
  width: 50%; /* Adjust the width as needed */
  height: auto;
}

.flex-container iframe {
  width: 50%; /* Adjust the width as needed */
  height: auto;
  aspect-ratio: 16 / 9; /* Maintain a 16:9 ratio for the video */
}

///This is for seminars

/* Dropdown Menu */
.dropdown {
  position: relative;
  display: inline-block;
  margin-bottom: 20px;
}

.dropbtn {
  background-color: #00664F; /* Evergreen */
  color: white;
  padding: 10px 20px;
  font-size: 16px;
  border: none;
  cursor: pointer;
  border-radius: 5px;
}

.dropbtn:hover {
  background-color: #004b28; /* Spruce */
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: white;
  min-width: 160px;
  box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.2);
  z-index: 1;
  border-radius: 5px;
}

.dropdown-content a {
  color: #004b28; /* Spruce */
  padding: 10px 16px;
  text-decoration: none;
  display: block;
  border-bottom: 1px solid #ddd;
}

.dropdown-content a:hover {
  background-color: #e5f3ee; /* Light Juniper background */
}

.dropdown:hover .dropdown-content {
  display: block;
}


/* Table Styles */
table {
  width: 100%;
  border-collapse: collapse;
  margin: 20px 0;
  font-size: 16px;
  text-align: left;
}

table th, table td {
  padding: 12px 15px;
  border: 1px solid #ddd;
}

table th {
  background-color: #00664F; /* Evergreen */
  color: white;
}

table tr:nth-child(even) {
  background-color: #e5f3ee; /* Light Juniper */
}

table tr:hover {
  background-color: #b7d9cc; /* Soft Juniper */
}

/*button for opprotunities */

.button {
  background-color: #00664F; /* Evergreen */
  border: none;
  color: white;
  padding: 19px 30px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 20px;
}

.button {border-radius: 12px;}

.button {transition-duration: 0.4s;
}

.button:hover {
  background-color: #04AA6D; /* Green */
  color: white;
}

.button:hover {
  box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
}


/*Timelines*/
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
}

.timeline-wrapper {
  position: relative;
  width: 90%;
  margin: 50px auto;
  height: 300px;
}

.middle-line {
  position: absolute;
  width: 100%;
  height: 4px;
  background: #ccc;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  z-index: 1;
}

.timeline {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  z-index: 2;
  height: 100%;
}

.box {
  position: relative;
  width: 20%;
  min-width: 150px;
}

.date {
  width: 100px;
  height: 50px;
  background: #fff;
  border: 2px solid #999;
  border-radius: 50%;
  text-align: center;
  line-height: 50px;
  margin: 0 auto;
  font-weight: bold;
}

.box-content {
  background: #fff;
  border: 1px solid #ccc;
  padding: 10px;
  margin-top: 10px;
  text-align: center;
  box-shadow: 0 0 5px rgba(0,0,0,0.1);
}

.box .box-content {
  border-radius: 5px;
  background-color: #1a7e50;
  width: 180px;
  position: absolute;
  left: -77px;
  padding: 15px;
}
.box-content p {
  margin: 0;
  color: white;
}


/* Alternate: place content above the line */
.box:nth-child(even) .box-content {
  top: auto;
  bottom: 110%;
}


.box-content::before {
  content: " ";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  border: 10px solid transparent;
}

.box-bottom .box-content::before {
  border-bottom-color: #00b0bd;
  top: -20px;
}
.box-top .box-content::before {
  border-top-color: #00b0bd;
  bottom: -20px;
}




